/* ------------------------------------------------------------ *\
	Base
\* ------------------------------------------------------------ */

html { margin-top: 0; -webkit-transition: margin-top .4s; -o-transition: margin-top .4s; transition: margin-top .4s; }

html.campaign { margin-top: 60px; }

html.campaign.hide { margin-top: 0; }

html.campaign.show { margin-top: 60px; }

body { height: 100%; overflow-y: auto; -webkit-overflow-scrolling: touch; min-width: 320px; font-family: 'Poppins', sans-serif; font-size: 14px; line-height: 1.79; color: #353535; background: #fff; }

a { color: inherit; text-decoration: underline; }

a[href^="tel"] { text-decoration: none; }

h1, .h1 { font-family: 'Tungsten Rounded', sans-serif; font-size: 180px; line-height: 0.78; font-weight: 600; text-transform: uppercase; }

h1 small,
.h1 small { display: block; margin-bottom: 25px; font-size: 40px; font-weight: 400; }

h2, .h2 { font-family: 'Tungsten Rounded', sans-serif; font-size: 140px; line-height: 1; font-weight: 600; text-transform: uppercase; }

h2 span,
.h2 span { display: block; margin-bottom: -11px; font-size: 80px; font-weight: 400; }

h2 em,
.h2 em { font-weight: 300; font-style: normal; }

h2 strong,
.h2 strong { display: block; font-weight: 600; }

h3, .h3 { font-family: 'Tungsten Rounded', sans-serif; font-size: 65px; line-height: 0.93; font-weight: 600; letter-spacing: 0.01em; }

h4, .h4 { font-family: 'Tungsten Rounded', sans-serif; font-size: 40px; line-height: 1; font-weight: 500; letter-spacing: 0.01em; }

h5, .h5 { font-family: 'Tungsten Rounded', sans-serif; font-size: 20px; line-height: 1; font-weight: 300; letter-spacing: 0.05em; }

h6, .h6 { font-size: 16px; font-weight: 400; }

button { overflow: visible; }

h1,
h2,
h3,
h4,
h5,
h6 { margin-bottom: calc(1.79em / 2); }

p,
ul,
ol,
dl,
table,
blockquote { margin-bottom: 1.79em; }

h1[class],
h2[class],
h3[class],
h4[class],
h5[class],
h6[class],
h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child,
p:last-child,
ul:last-child,
ol:last-child,
dl:last-child,
table:last-child,
blockquote:last-child { margin-bottom: 0; }

/* Small Desktop */

@media (max-width: 1366px) {
	h1 { font-size: 160px; }
	h2 { font-size: 120px; }
}

/* Tablet Portrait */

@media (max-width: 1023px) {
	h6 { font-size: 14px; }
}

/* Mobile */

@media (max-width: 767px) {
	h1 { font-size: 100px; }
	h1 small { font-size: 35px; }
	h3 { font-size: 55px; }
	h4 { font-size: 35px; }

}

