.col--size1 { max-width: 48.6%; -ms-flex-preferred-size: 48.6%; flex-basis: 48.6%; }

/* Mobile */

@media (max-width: 767px) {
	.cols { -ms-flex-wrap: wrap; flex-wrap: wrap; }
	.col--size1 { max-width: 100%; -ms-flex-preferred-size: 100%; flex-basis: 100%; }
}

