/* ------------------------------------------------------------ *\
	Animations
\* ------------------------------------------------------------ */

/*  Logo  */

@-webkit-keyframes rotation {
	0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	50% { -webkit-transform: rotate(-45deg); transform: rotate(-45deg); }
}

@keyframes rotation {
	0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	50% { -webkit-transform: rotate(-45deg); transform: rotate(-45deg); }
}

@-webkit-keyframes rotationCopy {
	0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	50% { -webkit-transform: rotate(-45deg); transform: rotate(-45deg); }
}

@keyframes rotationCopy {
	0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	50% { -webkit-transform: rotate(-45deg); transform: rotate(-45deg); }
}

@-webkit-keyframes rotationSec {
	0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	50% { -webkit-transform: rotate(45deg); transform: rotate(45deg); }
}

@keyframes rotationSec {
	0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	50% { -webkit-transform: rotate(45deg); transform: rotate(45deg); }
}

@-webkit-keyframes rotationSecCopy {
	0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	50% { -webkit-transform: rotate(45deg); transform: rotate(45deg); }
}

@keyframes rotationSecCopy {
	0%, 100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	50% { -webkit-transform: rotate(45deg); transform: rotate(45deg); }
}

/*  Checkboxes  */

@-webkit-keyframes scale {
	0% { -webkit-transform: scale(.3); transform: scale(.3); }
	40% { -webkit-transform: scale(1.3); transform: scale(1.3); }
	100% { -webkit-transform: scale(1); transform: scale(1); }
}

@keyframes scale {
	0% { -webkit-transform: scale(.3); transform: scale(.3); }
	40% { -webkit-transform: scale(1.3); transform: scale(1.3); }
	100% { -webkit-transform: scale(1); transform: scale(1); }
}

/*  Slider Counter  */

@-webkit-keyframes width {
	from { width: 0; }
	to { width: 100%; }
}

@keyframes width {
	from { width: 0; }
