\* ------------------------------------------------------------ */

.mfp-card-image-holder.mfp-bg {
	background: #000;
	opacity: .85;
}

.mfp-card-image-holder .mfp-close {
	font-size: 38px;
	right: 7px;
}

/* ------------------------------------------------------------ *\
	Custom Scrollbar
\* ------------------------------------------------------------ */

.mCustomScrollBox { max-width: 200%; padding-right: 60px; margin-right: -60px; }

.mCSB_inside > .mCSB_container { margin: 0; }

.mCSB_scrollTools { width: 16px; right: 19px; border: 2px solid #d7d7d7; border-radius: 16px; opacity: 1; }

.mCSB_scrollTools .mCSB_draggerContainer { top: 5px; bottom: 5px; }

.mCSB_scrollTools .mCSB_draggerRail { background: none; }

.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar { width: 6px; border-radius: 6px; background: #d9d9d9; }

.mCSB_scrollTools .mCSB_dragger.mCSB_dragger_onDrag .mCSB_dragger_bar,
.mCSB_scrollTools .mCSB_dragger:active .mCSB_dragger_bar { background: #353535; }

/* ------------------------------------------------------------ *\
	Banner
\* ------------------------------------------------------------ */

div[class*="banner-"] { position: fixed; left: 0; width: 100%; z-index: 2; display: -webkit-box; display: -ms-flexbox; display: flex; -webkit-box-pack: center; -ms-flex-pack: center; justify-content: center; -webkit-box-align: center; -ms-flex-align: center; align-items: center; padding: 0 50px; -webkit-transition: -webkit-transform .4s; transition: -webkit-transform .4s; -o-transition: transform .4s; transition: transform .4s; transition: transform .4s, -webkit-transform .4s; z-index: 5000; }

div[class*="banner-"] > p { margin: 0; position: relative; padding-top: 3px; padding-right: 30px; }

div[class*="banner-"] > p .ico-hand-pointing { position: absolute; top: 50%; left: 0;  -webkit-transform: translateY(-50%);  -ms-transform: translateY(-50%);  transform: translateY(-50%); }

div[class*="banner-"] > p .ico-hand-okay { position: absolute; right: 0; top: 50%; -webkit-transform: translateY(-50%); -ms-transform: translateY(-50%); transform: translateY(-50%);  }

div[class*="banner-"] .banner__close { position: absolute; top: 50%; right: 30px; -webkit-transform: translateY(-50%); -ms-transform: translateY(-50%); transform: translateY(-50%); text-decoration: none !important; border: 0; }

/*  Modifier: Banner Campaign */

.banner-campaign { top: 0; font-size: 16px; background: #fff2ba; color: #353535; -webkit-transform: translateY(-60px); -ms-transform: translateY(-60px); transform: translateY(-60px); height: 60px; font-weight: 500; }

.banner-campaign .btn { border-radius: 4px; padding: 6px 21px; border: 2px solid #353535; -webkit-transition: color .3s, background .3s, border-color .3s; -o-transition: color .3s, background .3s, border-color .3s; transition: color .3s, background .3s, border-color .3s; }

.banner-campaign .btn:hover { background: #353535; color: #FFE26D; }

.banner-campaign a:not(.btn):not(.banner__close) { text-decoration: none; letter-spacing: .010em; font-size: 16px; border-bottom: 1px solid #353535; line-height: 1; -webkit-transition: all .4s; -o-transition: all .4s; transition: all .4s; font-weight: 500; }

.banner-campaign a:not(.btn):not(.banner__close):hover { color: #00a0d2; border-bottom-color: #00a0d2; }

.banner-campaign a:not(.btn):not(.banner__close) span { color: #00a0d2; border-bottom: 1px solid #00a0d2;}

.banner-campaign > p { padding: 3px 30px 0 45px; text-align: center; }

.banner-campaign > p span { margin-left: 15px; letter-spacing: 0.03em; }

html.campaign .banner-campaign {-webkit-transform: translateY(0px);-ms-transform: translateY(0px);transform: translateY(0px); position: fixed; top: 0; left: 0; width: 100%; z-index: 1100; }

html.campaign body.nav-visible .banner-campaign { z-index: 2; }

html.campaign.hide .banner-campaign { -webkit-transform: translateY(-60px); -ms-transform: translateY(-60px); transform: translateY(-60px);}

html.campaign.show .banner-campaign { -webkit-transform: translateY(0); -ms-transform: translateY(0); transform: translateY(0);}

/*  Modifier: Banner Policy */

div.banner-policy { display: block; width: 340px; bottom: 32px; right: 25px; left: unset; padding: 16px 15px; background: #fff; color: #000; -webkit-transform: translateY(calc(100% + 50px)); -ms-transform: translateY(calc(100% + 50px)); transform: translateY(calc(100% + 50px)); font-family: 'Inter', sans-serif; border-radius: 8px; -webkit-box-shadow: 16px 16px 32px rgba(0, 0, 0, 0.1); box-shadow: 16px 16px 32px rgba(0, 0, 0, 0.1);; }

div.banner-policy .btn span { white-space: nowrap; background: rgba(255, 255, 255, .7); color: #000; border-color: #000; }

div.banner-policy p { margin-bottom: 15px; padding: 0 25px 0 0; text-align: left; font-size: 13px; line-height: 1.84; }

div.banner-policy p a { text-decoration: underline;}

div.banner-policy .banner__close { width: 20px; height: 32px; top: 25px; right: 8px; padding: 0 19px; color: #353535; font-size: 14px; }

div.banner-policy .banner__btn { font-size: 14px; }

html.show-banner-policy .banner-policy { -webkit-transform: translateY(0%); -ms-transform: translateY(0%); transform: translateY(0%); }

/*  Modifier: Banner Toggle */

div.banner-toggle { height: 100%; -webkit-transform: translateY(-100%); -ms-transform: translateY(-100%); transform: translateY(-100%); top: 0; -webkit-box-orient: vertical; -webkit-box-direction: normal; -ms-flex-direction: column; flex-direction: column; background: #fff2ba; overflow: hidden; }

html.campaign .banner-toggle { -webkit-transform: translateY(calc(-100% + 71px)); -ms-transform: translateY(calc(-100% + 71px)); transform: translateY(calc(-100% + 71px));}

html.campaign .banner-toggle.show { -webkit-transform: translateY(0); -ms-transform: translateY(0); transform: translateY(0); }

.banner-toggle .banner__content { position: relative; max-width: 800px; padding: 20px 20px 120px; margin: 0 auto; text-align: center; }

.banner-toggle .banner__content .banner__icon { margin-bottom: 40px; }

.banner-toggle .banner__content .banner__icon i { width: 56px; height: 56px; }

.banner-toggle .banner__title { position: relative; margin-bottom: 40px; padding-bottom: 30px; font-size: 45px; line-height: 1.23; font-weight: normal; text-transform: unset; }

.banner-toggle .banner__title:before { content: ''; position: absolute; left: 50%; -webkit-transform: translateX(-50%); -ms-transform: translateX(-50%); transform: translateX(-50%); width: 70px; height: 8px; bottom: 0; background-image: url(../public/assets/images/wave-charcoal.png); background-size: 100% 100%; }

.banner-toggle .banner__title strong { display: inline; }

.banner-toggle .banner__toggle { position: relative; margin-left: 8px; }

.banner-toggle .banner__toggle:after { content: ''; margin-left: 5px; display: inline-block; width: 9px; height: 9px; border-left: 1.5px solid #353535; border-bottom: 1.5px solid #353535; -webkit-transform: rotate(-45deg); -ms-transform: rotate(-45deg); transform: rotate(-45deg); -webkit-transition: border-color .4s; -o-transition: border-color .4s; transition: border-color .4s; }

.banner-toggle .banner__content .banner__toggle { z-index: 5; position: absolute; bottom: 15px; left: 50%; margin: 0; -webkit-transform: translateX(-50%); -ms-transform: translateX(-50%); transform: translateX(-50%); visibility: hidden; opacity: 0; -webkit-transition: opacity .4s; -o-transition: opacity .4s; transition: opacity .4s; }

.banner-toggle.show .banner__content .banner__toggle { visibility: visible; opacity: 1; }

.banner-toggle .banner__content .banner__toggle:after { top: 9px; -webkit-transform: rotate(135deg); -ms-transform: rotate(135deg); transform: rotate(135deg) }

.banner-toggle .banner__actions { display: -webkit-box; display: -ms-flexbox; display: flex; -webkit-box-align: center; -ms-flex-align: center; align-items: center; height: 71px; position: absolute; bottom: 0; padding: 20px; -webkit-transition: all .8s; -o-transition: all .8s; transition: all .8s; }

.banner-toggle.show .banner__actions { opacity: 0; }

div.banner-toggle .banner__close { z-index: 5; top: 38px; right: 38px; }

@media (max-height: 850px) {
	html.campaign .banner-toggle.show .banner__body { overflow-y: scroll; }
}

@media(max-width: 1023px){
	div.banner-campaign { -ms-flex-wrap: wrap; flex-wrap: wrap; height: auto; padding: 9px 40px 10px 39px!important; }
	div.banner-campaign > p { padding: 0 20px; }
	div.banner-campaign .banner__close { right: 20px; }

	html.campaign { margin-top: 71px; }
	html.campaign .header { top: 71px; }
	html.campaign .banner-toggle { padding: 0 15px; }
	.banner-toggle .banner__actions { height: 74px; padding: 10px 20px; text-align: center; }

  }

@media(max-width: 767px){
	.banner-campaign { -webkit-transform: translateY(-90px); -ms-transform: translateY(-90px); transform: translateY(-90px); }
	div.banner-campaign > p { -webkit-box-flex: 0; -ms-flex: 0 0 100%; flex: 0 0 100%; padding: 0!important; line-height: 1.2; margin-bottom: 10px; }
	div.banner-campaign > p span { margin-left: 0; }
	div.banner-campaign > p .ico-code-alt { display: none;  }

	html.campaign { margin-top: 106px; }
	html.campaign .header { top: 106px; }
	html.campaign .banner-toggle{ -webkit-transform: translateY(calc(-100% + 108px)); -ms-transform: translateY(calc(-100% + 108px)); transform: translateY(calc(-100% + 108px)); }
	.banner-toggle .banner__actions { height: 108px; padding: 15px 20px; }
	.banner-toggle .banner__content { display: none; }
	.banner-toggle.show .banner__content { display: block; }
	.banner-toggle .banner__title { font-size: 38px; }

	div.banner-policy { width: 280px; bottom: 20px; right: 20px; }
}

@media(max-width: 480px){
	.banner-campaign { -webkit-transform: translateY(-107px); -ms-transform: translateY(-107px); transform: translateY(-107px); }
	div[class*="banner-"] { padding-left: 10px; }
	.banner-campaign a:not(.btn):not(.banner__close),
	div[class*="banner-"] a { font-size: 13px; }
	div[class*="banner-"] > p { padding: 0 0 0 35px; }
	div.banner-policy > p { margin-bottom: 10px; padding: 0 25px 0 0; }
	div[class*="banner-"] > p .ico-hand-okay { display: none;  }

	html.campaign .banner-toggle{ -webkit-transform: translateY(calc(-100% + 118px)); -ms-transform: translateY(calc(-100% + 118px)); transform: translateY(calc(-100% + 118px)); }
	.banner-toggle .banner__actions { height: 118px; padding: 7px;  }
	.banner-toggle .btn > span { padding: 0 20px; }
	.banner-toggle .banner__title { font-size: 36px; }
}

@media (max-width: 370px) {
	.banner-toggle .banner__title { font-size: 32px; }
}

/* ------------------------------------------------------------ *\
	Voucher
\* ------------------------------------------------------------ */

.voucher {  }

.voucher .voucher__image { max-width: 609px; margin: 0 auto 70px; position: relative; z-index: 5;  }

.voucher .voucher__image:before { content: ''; position: absolute; top: 0; bottom: 0; left: 13px; right: 13px; -webkit-box-shadow: 32px 32px 65px rgba(0, 0, 0, .15); box-shadow: 32px 32px 65px rgba(0, 0, 0, .15); border-radius: 26px; z-index: -1; }

.voucher .voucher__data { list-style: none; text-align: center; font-size: 16px; line-height: 1.88;  }

.voucher .voucher__data li { padding: 0 52px; position: relative; }

.voucher .voucher__data li i { position: absolute; top: 0; left: 0; }

.voucher .voucher__data li + li { margin-top: 20px;  }

@media (max-width: 767px) {
	.voucher .voucher__data { font-size: 14px;  }
	.voucher .voucher__data li { padding-right: 0;  }
}

